import React, { Component } from 'react'
import { Carousel,Col, Row,Divider, Tag } from 'antd';
import "./Newsone.css"
import qs from 'qs'

export default class Newsone extends Component {
  state={
    shopone:[
      {id:1,images:"shoplist1.jpg",newstitle:"TTC RK98",riqi:"2022.01.04",jj:"RK96RK98TTCPA98 PBT TTCRK9696RK96RK98TTCPA98"},
      {id:2,images:"shoplist2.jpg",newstitle:"98 RK96 RGB",riqi:"2022.01.04",jj:"98RK98RK98RK96RK98TTCPA98 PBT TTC"},
      {id:3,images:"shoplist3.jpg",newstitle:"96 RK860",riqi:"2022.01.04",jj:"RK9696RK96RK98TTCPA98 PBT TTC"},
    ], 
    sxsju:[

    ], 
  }
  componentDidMount(){
    const  {id,}=this.props.match.params
    document.documentElement.scrollTop = document.body.scrollTop =0;//

    const newcpone=this.state.shopone.find(value=>value.id==id)
    // console.log(newcpone)
    this.setState({sxsju:newcpone})

  }
  fh=()=>{
    this.props.history.go(-1)
  }

  render() {
    const  {id,title}=this.props.match.params
    return (
      <div className='newsone'>
        <div className="t-title">
            <img src={window.tubase+"24-50.png"}></img>
            <span>NEWS</span>
            <p className="text"></p>
        </div>
        <img src={window.tubase+"newslist1.png"}></img>
        <div className='shoptitle'>
            <p></p>
            <span onClick={this.fh}></span>
        </div>
        <div className='shoplist'>
                <h1>{this.state.sxsju.newstitle}</h1>
                <p>{this.state.sxsju.riqi}</p>
                <p className='jj'>{this.state.sxsju.jj}</p>
                <img src={window.tubase+this.state.sxsju.images}></img>
        </div>



      </div>
    )
  }
}
